body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

*{margin:0}body{background-color:#f5f6fa}.app{display:flex;justify-content:space-evenly}.app__dropdown{background-color:white}.app__header{display:flex;align-items:center;margin-bottom:20px;justify-content:space-between}.app__header h1{color:#fc3c3c;font-size:2rem}.app__left{flex:0.9 1}.app__right{display:flex;flex-direction:column}.app__graph{flex-grow:1}.app__graphTitle{margin-top:20px;margin-bottom:20px}.app__stats{display:flex;justify-content:space-between}@media (max-width: 990px){.app{flex-direction:column}}

.infobox{flex:1 1;cursor:pointer}.infobox:not(:last-child){margin-right:15px}.infobox__cases{color:#cc1034;font-weight:600;font-size:1.75rem;margin-bottom:0.5rem}.infobox__cases--green{color:lightgreen !important}.infobox__total{color:#6c757d;font-weight:700 !important;font-size:0.8rem;margin-top:15px}.infobox--selected{border-top:10px solid greenyellow}.infobox--red{border-color:red}

.map{height:500px;background-color:white;padding:1rem;border-radius:20px;margin-top:16px;box-shadow:0 0 8px -4px rgba(0,0,0,0.5)}.map .leaflet-container{height:100%}.info-container{width:150px}.info-container__name{font-size:20px;font-weight:bold;color:#555}.info-container__flag{height:80px;width:100%;background-size:cover;border-radius:8px}.info-container__flag img{width:100px;border-radius:5px}.info-container__confirmed,.info-container__recovered,.info-container__deaths{font-size:16px;margin-top:5px}

.table{margin-top:20px;overflow:scroll;height:400px;color:#6a5d5d;background-color:white}.table tr{display:flex;justify-content:space-between}.table td{padding:0.5rem}.table tr:nth-of-type(odd){background-color:#f3f2f8}

